/* client/src/styles/UserDashboard.css */

/* Basic Resets & Global Styles */
html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow-x: hidden; /* Prevent horizontal scroll */
}

.user-dashboard-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    font-family: 'Arial', sans-serif;
    background-color: #f0f2f5; /* Light grey background for the page */
    color: #333;
    line-height: 1.6;
}

/* --- TOP NAVIGATION BAR STYLING (Disaster Relief Platform, Home, etc.) --- */
.top-navbar {
    background-color: #003366; /* Darker blue for the very top header */
    color: white;
    padding: 20px 20px;
    display: flex;
    justify-content: space-between; /* Space out left and right content */
    align-items: center;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.top-navbar-left h1 {
    margin: 0;
    font-size: 1.5em; /* Increased size for "Disaster Relief Platform" */
    font-weight: 600;
    white-space: nowrap; /* Prevent text wrapping */
}

.top-navbar-right a {
    color: white;
    text-decoration: none;
    margin-left: 25px; /* Increased space between links */
    font-size: 5.2em; /* Increased size for Home, Volunteer, Admin, Login */
    font-weight: 500;
    transition: color 0.2s ease;
}

.top-navbar-right a:hover {
    color: #a0dfff; /* Lighter blue on hover */
}

/* --- END TOP NAVIGATION BAR STYLING --- */


/* Main Green Header Styling (ResQLink) */
.user-header {
    background-color: #28a745; /* Green header */
    color: white;
    padding: 15px 20px;
    display: flex;
    justify-content: center; /* Center the title */
    align-items: center;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    
    width: 100%; /* Ensure it takes full available width */
    max-width: none; /* Remove max-width restriction */
    margin: 0; /* Remove auto margin for full width */
}

.user-header h1 {
    margin: 0;
    font-size: 2.2em; /* Size for "ResQLink (User Dashboard)" */
    font-weight: 600;
    text-align: center;
}

/* Main Content Area */
.user-main-content {
    flex: 1; /* Takes up remaining vertical space */
    display: flex;
    justify-content: center; /* Center content horizontally */
    align-items: center; /* Align items to center vertically */
    padding: 20px; /* Padding around the main card */
    box-sizing: border-box; /* Include padding in width */
}

/* Card Styling (for both form and status/chat panel) */
.card {
    background-color: #ffffff;
    border-radius: 12px;
    box-shadow: 0 6px 15px rgba(0,0,0,0.1);
    padding: 30px;
    width: 100%;
    max-width: 750px; /* Increased form size */
    box-sizing: border-box; /* Include padding in width */
    margin-top: auto; /* For vertical centering */
    margin-bottom: auto; /* For vertical centering */
}

.card h2 {
    color: #28a745; /* Green heading for sections */
    text-align: center;
    margin-bottom: 25px;
    font-size: 1.6em;
    border-bottom: 2px solid #eee;
    padding-bottom: 10px;
}

/* Info Message for user */
.info-message {
    text-align: center;
    margin-top: -15px; /* Pull it slightly up towards the heading */
    margin-bottom: 25px; /* Space below it */
    font-size: 0.95em;
    color: #666; /* Adjust color for light background */
    padding: 0 10px; /* Small horizontal padding */
}

/* Form Group (Label + Input/Textarea) */
.form-group {
    margin-bottom: 20px;
}

.form-group label {
    display: block;
    margin-bottom: 8px;
    font-weight: 600;
    color: #555;
}

.form-group input[type="text"],
.form-group input[type="tel"],
.form-group textarea {
    width: calc(100% - 20px); /* Account for padding */
    padding: 10px;
    border: 1px solid #ced4da;
    border-radius: 6px;
    font-size: 1em;
    transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.form-group input:focus,
.form-group textarea:focus {
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0,123,255,.25);
}

.form-group textarea {
    resize: vertical; /* Allow vertical resizing */
}

/* Buttons */
.get-location-btn,
.submit-sos-btn,
.cancel-sos-btn,
.chat-send-btn {
    display: block; /* Make buttons full width */
    width: 100%;
    padding: 12px 20px;
    border: none;
    border-radius: 8px;
    font-size: 1.1em;
    cursor: pointer;
    transition: background-color 0.2s ease, transform 0.2s ease;
    margin-top: 15px; /* Space between form elements and buttons */
}

.get-location-btn {
    background-color: #007bff; /* Blue for Get Location */
    color: white;
}

.get-location-btn:hover {
    background-color: #0056b3;
    transform: translateY(-2px);
}

.submit-sos-btn {
    background-color: #dc3545; /* Bright Red for Send SOS */
    color: white;
    font-weight: bold;
    padding: 18px 25px; /* Make it bigger */
    font-size: 1.4em; /* Larger text */
    box-shadow: 0 6px 12px rgba(220, 53, 69, 0.4); /* Prominent shadow */
}

.submit-sos-btn:hover {
    background-color: #c82333; /* Darker red on hover */
    transform: translateY(-3px); /* More pronounced lift effect */
    box-shadow: 0 8px 16px rgba(220, 53, 69, 0.5); /* Stronger shadow on hover */
}

.submit-sos-btn:disabled {
    background-color: #ccc;
    box-shadow: none; /* No shadow when disabled */
    cursor: not-allowed;
    transform: none;
}

.cancel-sos-btn {
    background-color: #6c757d; /* Grey for Cancel SOS */
    color: white;
    font-weight: bold;
    margin-top: 20px; /* More space for a crucial action */
}

.cancel-sos-btn:hover {
    background-color: #5a6268;
    transform: translateY(-2px);
}

/* Location Info */
.location-info {
    text-align: center;
    margin-top: 10px;
    font-size: 0.9em;
    color: #666;
    font-style: italic;
}

/* SOS Status & Chat Panel */
.sos-status-chat-panel {
    display: flex;
    flex-direction: column;
    height: auto; /* Allow height to adjust */
    max-height: 80vh; /* Max height to prevent overflow on very tall content */
}

.status-display {
    text-align: center;
    padding-bottom: 20px;
    border-bottom: 1px solid #eee;
    margin-bottom: 20px;
}

.status-display p {
    font-size: 1.1em;
    margin-bottom: 5px;
}

/* Status Badges for User Dashboard (similar to Admin for consistency) */
.status-badge {
    display: inline-block;
    padding: 5px 10px;
    border-radius: 15px;
    font-size: 0.9em;
    font-weight: 600;
    color: white;
    text-transform: capitalize;
    min-width: 80px; /* Ensure badges have a consistent width */
    text-align: center;
}

.status-pending { background-color: #ffc107; color: #333; } /* Yellow */
.status-accepted { background-color: #007bff; } /* Blue */
.status-in-progress { background-color: #6f42c1; } /* Purple */
.status-completed { background-color: #28a745; } /* Green */
.status-cancelled { background-color: #6c757d; } /* Grey */


/* Chat Section */
.chat-section {
    flex-grow: 1; /* Allows chat to take remaining space */
    display: flex;
    flex-direction: column;
    min-height: 300px; /* Minimum height for chat */
}

.chat-section h3 {
    text-align: center;
    margin-top: 0;
    margin-bottom: 15px;
    color: #555;
    font-size: 1.3em;
}

.chat-box {
    flex-grow: 1; /* Allows chat messages to fill space */
    overflow-y: auto; /* Enable scrolling for chat messages */
    border: 1px solid #e9ecef;
    border-radius: 8px;
    padding: 15px;
    background-color: #fafafa;
    margin-bottom: 15px;
}

.chat-message {
    display: flex;
    margin-bottom: 10px;
    animation: fadeIn 0.3s ease-out; /* Simple fade-in animation for new messages */
}

.chat-bubble {
    padding: 10px 15px;
    border-radius: 18px;
    max-width: 75%;
    word-wrap: break-word;
    font-size: 0.95em;
    line-height: 1.4;
    box-shadow: 0 1px 2px rgba(0,0,0,0.1);
}

.user-message { /* Messages sent by the current user */
    justify-content: flex-end;
}

.user-message .chat-bubble {
    background-color: #d1e7dd; /* Light green for user's own messages */
    color: #0f5132;
}

.volunteer-message { /* Messages received from the volunteer */
    justify-content: flex-start;
}

.volunteer-message .chat-bubble {
    background-color: #e0f2fe; /* Light blue for volunteer's messages */
    color: #055160;
}

.chat-message p {
    margin: 0;
}

.no-messages {
    text-align: center;
    color: #888;
    font-style: italic;
    padding: 20px;
}

.chat-form {
    display: flex;
    gap: 10px;
    padding-top: 15px;
    border-top: 1px solid #eee;
    background-color: #fff; /* Ensure form background is consistent */
}

.chat-input {
    flex-grow: 1;
    padding: 10px;
    border: 1px solid #ced4da;
    border-radius: 6px;
    font-size: 1em;
}

.chat-send-btn {
    flex-shrink: 0; /* Prevent button from shrinking */
    width: auto; /* Allow button to size based on content */
    padding: 10px 20px; /* Adjust padding for button */
    margin-top: 0; /* Override default button margin-top */
    background-color: #28a745;
    color: white;
    border-radius: 6px;
}

/* Animations */
@keyframes fadeIn {
    from { opacity: 0; transform: translateY(10px); }
    to { opacity: 1; transform: translateY(0); }
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .user-main-content {
        padding: 15px; /* Slightly less padding on smaller screens */
        align-items: center; /* Center vertically on smaller screens */
    }

    .card {
        padding: 20px; /* Reduced padding inside cards */
        max-width: 95%; /* Allow card to take more width */
    }

    .top-navbar-left h1 {
        font-size: 1.2em; /* Adjusted for smaller screens */
    }
    .top-navbar-right a {
        font-size: 1em; /* Adjusted for smaller screens */
        margin-left: 10px;
    }

    .user-header h1 {
        font-size: 1.8em; /* Adjusted for smaller screens */
    }

    .info-message {
        font-size: 0.85em;
        margin-bottom: 20px;
    }

    .sos-form, .sos-status-container {
        padding: 25px;
        gap: 20px;
    }

    .form-group input,
    .form-group textarea {
        padding: 10px;
        font-size: 0.95em;
    }

    .get-location-btn, .submit-sos-btn, .cancel-sos-btn {
        padding: 14px 20px;
        font-size: 1.1em;
    }

    .chat-container {
        height: 400px;
        padding: 15px;
    }

    .chat-container h3 {
        font-size: 1.3em;
    }

    .chat-bubble {
        padding: 10px 15px;
        font-size: 0.9em;
    }

    .chat-form input,
    .chat-form button {
        padding: 10px;
        font-size: 0.9em;
    }
}
