.volunteer-dashboard {
  font-family: 'Poppins', sans-serif;
  background-color: #f0f2f5;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}

/* Header Styling */
.header {
  background-color: #1a5276;
  color: white;
  padding: 15px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.header h1 {
  margin: 0;
  font-size: 1.8em;
  font-weight: 600;
}

.logout-button {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 8px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.9em;
  transition: background-color 0.2s ease;
}

.logout-button:hover {
  background-color: #c82333;
}

/* Main Content Wrapper */
.main-content {
  padding: 20px;
  flex-grow: 1;
}

/* Welcome Message Section */
.welcome-container {
  background-color: #fff;
  padding: 25px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  margin-bottom: 20px;
}

.welcome-container h2 {
  margin: 0 0 10px;
  font-size: 1.5em;
  color: #1a5276;
}

.welcome-container p {
  margin: 0;
  color: #555;
  line-height: 1.5;
}

/* Flexbox Containers for Dashboard Panels */
.table-map-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  flex-grow: 1;
}

@media (min-width: 992px) {
  .table-map-container {
    flex-direction: row;
    align-items: stretch; /* Align items to the full height */
  }
}

.table-container-left,
.map-chat-container-right {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
}

.table-container-left {
  flex: 3;
}

.map-chat-container-right {
  flex: 2;
  gap: 20px;
}

/* Table Styling */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 15px;
}

table th, table td {
  padding: 12px 10px;
  border: 1px solid #ddd;
  text-align: left;
  vertical-align: top;
  word-wrap: break-word;
}

table th {
  background-color: #f0f2f5;
  font-weight: bold;
  color: #333;
  text-transform: uppercase;
  font-size: 0.85em;
}

table tr:nth-child(even) {
  background-color: #fafafa;
}

table tr:hover {
  background-color: #eef;
}

/* Action buttons */
table button, table select {
  padding: 6px 10px;
  border-radius: 4px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  font-size: 0.8em;
  transition: background-color 0.2s ease;
}

table button:hover {
  background-color: #0056b3;
}

table select {
  background-color: #fff;
  color: #333;
  border: 1px solid #ccc;
}

/* Map & Chat Containers */
.map-card-wrapper {
  flex: 1;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
}

.chat-container {
  flex: 1;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
  min-height: 250px;
}

.chat-container h3 {
  background-color: #f8f8f8;
  padding: 15px;
  margin: 0;
  border-bottom: 1px solid #eee;
  font-size: 1.1em;
  color: #333;
}

/* Chat box and form styling */
.chat-box {
  flex-grow: 1;
  overflow-y: auto;
  padding: 15px;
}

.chat-form {
  display: flex;
  gap: 10px;
  padding: 15px;
  border-top: 1px solid #eee;
  background-color: #f8f8f8;
}

.chat-form input {
  flex-grow: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.chat-form button {
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.chat-form button:hover {
  background-color: #218838;
}

/* Chat message bubbles */
.chat-message {
  display: flex;
  margin-bottom: 10px;
}

.chat-bubble {
  padding: 10px 15px;
  border-radius: 20px;
  max-width: 75%;
}

.user-message {
  justify-content: flex-start;
}

.user-message .chat-bubble {
  background-color: #e0f2fe;
  color: #333;
}

.volunteer-message {
  justify-content: flex-end;
}

.volunteer-message .chat-bubble {
  background-color: #dcf8c6;
  color: #333;
}